.locationCont{
    width: 100%;
    height: 55px;
    text-align: left;
    line-height: 55px;
    color: #888;
    overflow: hidden;
    cursor: pointer;
}

.locationCont h1{
    margin: 0;
    white-space: nowrap;
}

.weatherLocationCont{
    width: calc(100% - 20px);
    margin-left: 10px;
    margin-right: 10px;
    min-width: 350px;
    box-sizing: border-box;
    padding:15px;
    font-family: 'Roboto', sans-serif;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.75);
}
