

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.details{
    background: #999;
    height: 85vh;
   /* width: calc(100% - 40px); */
    overflow: auto;
    padding-left: 20px;
    padding-right: 20px;
}


@media (max-width: 64em){
    .details{
        height: 40vh!important;
    }
}

.locationCont{
    width: 100%;
    height: 55px;
    text-align: left;
    line-height: 55px;
    color: #888;
    overflow: hidden;
    cursor: pointer;
}

.locationCont h1{
    margin: 0;
    white-space: nowrap;
}

.weatherLocationCont{
    width: calc(100% - 20px);
    margin-left: 10px;
    margin-right: 10px;
    min-width: 350px;
    box-sizing: border-box;
    padding:15px;
    font-family: 'Roboto', sans-serif;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.75);
}

.weatherDataCont{
    background-color: rgba(200,200,200,0.5);
     width: 100%; 
    height:100px;
    line-height: 100px;
    text-align: center;
    border-radius: 5px;
    padding:5px;
    box-sizing: border-box;
}

.weatherExtraInfoCont{
    line-height: 28px;
    text-align: left;
    width:40%;
    display:inline-block;
}

.weatherTemperatureCont{
    width:60%;
    display:inline-block;
}

.temperature{
    font-size: 28px;
    font-weight: bold;
    padding-left:20px;
    color: #333;
}

.temperatureType{
    font-weight: bold;
    color: #666;
}

.extraInfoText{
    display:block;
    color:#1535C5;
    font-size: 16px;
    font-weight: bold;
}

.wicon{
    color: rgba(255,72,0);
}
.forecast-title{
    color:#fff;
    text-align: center;
    padding-top:15px;
    padding-bottom:15px;
}

@media (max-width: 64em){
    .locationList{
       
        max-height: 40vh;
        margin-bottom: 20px;
    }
}

.locationList{
       
    height: 85vh;
    overflow: auto;
    margin-bottom: 20px;
}
