.weatherDataCont{
    background-color: rgba(200,200,200,0.5);
     width: 100%; 
    height:100px;
    line-height: 100px;
    text-align: center;
    border-radius: 5px;
    padding:5px;
    box-sizing: border-box;
}

.weatherExtraInfoCont{
    line-height: 28px;
    text-align: left;
    width:40%;
    display:inline-block;
}

.weatherTemperatureCont{
    width:60%;
    display:inline-block;
}

.temperature{
    font-size: 28px;
    font-weight: bold;
    padding-left:20px;
    color: #333;
}

.temperatureType{
    font-weight: bold;
    color: #666;
}

.extraInfoText{
    display:block;
    color:#1535C5;
    font-size: 16px;
    font-weight: bold;
}

.wicon{
    color: rgba(255,72,0);
}